#home {
    overflow: none !important;
    width: 100%;
    height: 100vh;
    .background {
        position: absolute;
        height: 100vh;
    }
    .opacity {
        background-color: #000416bf;
        width: 100%;
        height: 100vh;
    }
    .fadein {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: url(../../../images/background.jpg) no-repeat 78% 50% fixed;
        -webkit-background-size: cover;
        -moz-background-size: cover;
        -o-background-size: cover;
        background-size: cover;
        animation: pulse 5s ease-in;
    }
    @keyframes pulse {
        0%,
        50% {
            opacity: 0;
        }
        100% {
            opacity: 1;
        }
    }
    .hr {
        display: block;
        border: 1px solid rgb(200, 200, 200);
        width: 100%;
        animation: grow-hr 2s ease-in-out;
    }
    @keyframes grow-hr {
        0%,
        30% {
            opacity: 0;
            width: 0%;
        }
        100% {
            width: 100%;
            opacity: 1;
            transform: rotate(180deg);
        }
    }
    .bottom-hr {
        float: right;
    }
    .title {
        position: relative;
        width: 90%;
        text-align: center;
        margin: 0 auto;
        z-index: 9;
        transform: translateY(35vh);
        font-family: "Raleway", sans-serif;
        color: rgb(200, 200, 200);
    }
    #header-text {
        animation: slide-header 4s cubic-bezier(0.42, 1.74, 0.62, 0.81);
    }
    @keyframes slide-header {
        0%,
        50% {
            opacity: 0;
            transform: scale(0.3);
        }
        85% {
            transform: scale(1.02);
        }
        100% {
            opacity: 1;
            transform: scale(1);
        }
    }
    #proffesion {
        margin: 30px;
    }
    #buttons {
        font-family: "Roboto", sans-serif;
        width: 90%;
        display: flex;
        justify-content: center;
        margin: 0 auto;
        .btn {
            padding: 8px 0;
            width: 8em;
            text-align: center;
            cursor: pointer;
            margin: 5px;
            color: rgb(200, 200, 200);
            border-radius: 5px;
            border: 1px solid rgb(200, 200, 200);
        }
    }
    #finally {
        animation: finally 5s ease-in-out;
    }
    @keyframes finally {
        0%,
        50% {
            opacity: 0;
            transform: scale(0.5);
        }
        95% {
            transform: scale(1.09);
        }
        100% {
            transform: scale(1);
            opacity: 1;
        }
    }
    #available {
        opacity: 0;
        position: absolute;
        top: 50px;
        width: 100vw;
        animation: glow 2s ease-in-out;
    }
    @keyframes glow {
        0% {
            opacity: 0;
            transform: scale(0.3);
        }
        50% {
            opacity: 0.8;
            transform: scale(1);
        }
        80%,
        100% {
            opacity: 0;
            transform: scale(0.3);
        }
    }
}